import { Button } from 'bootstrap';
import React from 'react';
import { MyButton } from './UI/Button';

export const Banner = () => {
    const btnCatalog = {
        btn: false,
        textBtn: " ",
        classBtn: "primary",
        sizeBtn: "large",
        link: "#products",
        additClasses: "mr-3"
    }

    const btnConsul = {
        btn: false,
        textBtn: " ",
        classBtn: "grey",
        sizeBtn: "large",
        link: "tel:89051027358",
    }

    return (
        <div id="banner">
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <div className="banner-textblock">
                            <h1><span></span> <br />     <span style={{display: "none"}}>-</span></h1>
                            <p>       . <span>       ,         .</span></p>
                        </div>
                        <div className="banner-buttonblock">
                            <MyButton {...btnCatalog}/>
                            <MyButton {...btnConsul}/>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="banner-imgblock">
                            <img src={require('../img/main.png')} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}